.body {
  box-sizing: border-box;
  margin: 0;
  padding: 0;
  font-family: Roboto, sans-serif;
  font-style: normal;
  font-weight: 500;
}

p {
  margin: 0;
  font-weight: 700;
}

.card {
  box-sizing: border-box;
  border: 1px solid #f3f3f3;
  border-radius: 5px;
  width: 200px;
  padding: 32px 16px 16px;
  transition: transform .3s, color .3s;
}

.card:hover {
  transform: scale(1.2);
}

.card__image {
  width: 160px;
  height: 134px;
  margin: 0 auto 40px;
  display: block;
}

.card__title {
  box-sizing: border-box;
  color: #060b35;
  margin-bottom: 4px;
  font-size: 12px;
  font-weight: 500;
  line-height: 18px;
  transition: color .3s;
}

.card__title:hover {
  color: #34568b;
}

.card__subtitle {
  box-sizing: border-box;
  color: #616070;
  margin: 0 0 16px;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__response {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  width: 166px;
  margin: 0 0 24px;
  display: flex;
}

.card__response .stars {
  display: flex;
}

.card__response .stars__star {
  background-image: url("star.822d561e.svg");
  background-position: center;
  background-repeat: no-repeat;
  width: 16px;
  height: 16px;
  margin-right: 4px;
}

.card__response .stars__star:nth-child(-n+4) {
  background-image: url("star-active.edda4c43.svg");
}

.card__text {
  color: #060b35;
  font-size: 10px;
  font-weight: 400;
  line-height: 14px;
}

.card__price {
  justify-content: space-between;
  align-items: center;
  width: 100%;
  margin-bottom: 16px;
  line-height: 18px;
  display: flex;
}

.card__price .left {
  color: #616070;
  font-size: 12px;
  font-weight: 500;
}

.card__buy {
  box-sizing: border-box;
  color: #fff;
  background-color: #00acdc;
  border-radius: 5px;
  justify-content: center;
  align-items: center;
  width: 100%;
  height: 40px;
  font-size: 14px;
  font-weight: 700;
  line-height: 16px;
  text-decoration: none;
  transition: background-color .3s, color .3s;
  display: flex;
}

.card__buy:hover {
  color: #00acdc;
  background-color: #fff;
  border: 1px solid #00acdc;
}

.header {
  box-sizing: border-box;
  justify-content: space-between;
  align-items: center;
  height: 60px;
  margin: 0 auto;
  padding: 0 50px;
  display: flex;
}

.header__logo {
  width: 40px;
  height: 40px;
  display: flex;
}

.header__navigation {
  align-items: center;
  display: flex;
}

.header__list {
  text-transform: uppercase;
  gap: 20px;
  margin: 0;
  padding: 0;
  font-size: 12px;
  font-weight: 500;
  list-style-type: none;
  display: flex;
}

.header__link {
  color: #060b35;
  line-height: 40px;
  transition: color .3s;
  display: block;
  position: relative;
}

.header__link .is-active:hover:after {
  content: " ";
  background-color: #00acdc;
  border-radius: 8px;
  width: 100%;
  height: 4px;
  position: absolute;
  bottom: 0;
  left: 0;
}

.header__link:hover > a {
  color: #00acdc;
}

.header__link:active > a {
  cursor: pointer;
  color: #00acdc;
}

.main {
  box-sizing: border-box;
  grid-template-columns: repeat(1, 200px);
  justify-content: center;
  gap: 46px 48px;
  padding: 50px 40px;
  display: grid;
}

@media (width >= 488px) {
  .main {
    grid-template-columns: repeat(2, 200px);
  }
}

@media (width >= 768px) {
  .main {
    grid-template-columns: repeat(3, 200px);
  }
}

@media (width >= 1024px) {
  .main {
    grid-template-columns: repeat(4, 200px);
  }
}

@font-face {
  font-family: Roboto;
  src: url("Roboto-Medium.b7336e49.ttf");
  font-weight: 500;
  font-style: normal;
}

body {
  margin: 0;
  font-family: Roboto, sans-serif;
}
